import Vue from 'vue'
import { Notify } from 'vant'

Vue.use(Notify)

class ValidForm {


 valid(o) {
  for (let key in o) {
   if (!o[key].reg.test(o[key].value)) {
    //提示
    Notify({
     type: 'warning',
     message: o[key].errorMsg
    })
    //验证失败
    return false;
   }
  }

  //验证通过
  return true;
 }

}

export const validForm = new ValidForm();